ul {
	padding-left: 0;
}

p {
	margin-bottom: 0;
}

.flex {
	display: flex;
}

.left-2 {
	padding-left: 2em;
}

.background-image {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
}

.circle-image {
	border-radius: 50%;
}

img.noresp {
	max-width: none;
	width: auto;
}


@mixin boxshadow {
	-webkit-box-shadow: 0px 0px 17px 0px rgba(117,13,55,0.23);
	-moz-box-shadow:    0px 0px 17px 0px rgba(117,13,55,0.23);
	box-shadow:         0px 0px 17px 0px rgba(117,13,55,0.23);
}